@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Montserrat:regular);
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a {
  color: black;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
} /*Крестик в поле input браузера IE отключен*/
button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
} /*Отступы кнопок браузера Firefox 0*/
a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

/*----------------------*/
body {
  background: hsla(215, 41%, 18%, 0.5);
  font-family: "Montserrat";
  position: relative;
}

hr {
  height: 1px;
  background: linear-gradient(90deg, rgba(255,255,255,0) 10%, rgba(27,43,65,1) 50%, rgba(255,255,255,0) 90%);
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

/* Welcome page */
.hello__title {
  margin-bottom: 50px;
  font-weight: 700;
  text-align: center;
  font-size: 30px;
}

.pt_25 {
  padding-top: 25px;
}

.form {
  margin: 0 auto;
  padding: 15px;
  width: 95%;
  max-width: 500px;
  height: fit-content;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgb(128, 128, 128);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  font-size: 25px;
  gap: 10px;
  box-shadow: 0px 0px 10px 0px hsla(215, 41%, 18%, 0.5);
}

.form__label {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
}

form input {
  padding: 5px 25px;
  width: 100%;
  height: 50px;
  background: hsla(0, 0%, 100%, 0.8);
  font-size: 18px;
  border: 1px solid rgb(128, 128, 128);
  border-radius: 15px;
  transition: all 0.4s ease;
}
form input:focus {
  box-shadow: 0px 0px 15px rgb(27 43 65 / 60%);;
}

.form__submit {
  padding: 10px 25px;
  margin: 0 auto;
  width: fit-content;
  height: fit-content;
  font-size: 18px;
  color: #000;
  background: hsla(0, 0%, 100%, 0.8);
  border: 1px solid rgb(128, 128, 128);
  border-radius: 15px;
  text-transform: uppercase;
  transition: all 0.4s ease;
}
.form__submit:disabled,
.form__submit:disabled:hover, 
.form__submit:disabled:focus {
  cursor: default;
  box-shadow: none;
  color: gray;
}

.register {
  text-align: center;
  font-size: 18px;
}

.register a:hover {
  border-bottom: 1px solid rgb(27 43 65 / 60%);;
}

.form__submit:hover, .form__submit:focus {
  box-shadow: 0px 0px 15px rgb(27 43 65 / 60%);;
}

/* ERRORS */
.error {
  padding: 5px 15px;
  width: max-content;
  position: absolute;
  left: 50%;
  z-index: 2;
  transform: translate(-50%, -50%);
  font-size: 16px;
  color: red;
  background: white;
  border: 1px solid rgb(128, 128, 128);
  border-radius: 10px;
}

/* FONT_SIZE */
.fz_16 {
  font-size: 16px;
}

/* MARGINS */
.mb_15 {
  margin-bottom: 15px;
}
.ml_15 {
  margin-left: 15px;
}
/* STYLES FOR PASS INPUT */

.input-group {
  position: relative;
  display: flex;
  align-items: center;
}

.toggle {
  width: 35px;
  height: 25px;
  position: absolute;
  right: 10px;
  cursor: pointer;
  user-select: none;
  z-index: 99;
  transform: translate(0, -50%);
  top: 50%;
}

.pass-strength {
  margin-top: 3px;
  padding: 0 49px 0 10px;
  width: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 10%;
}

.strength-label {
  font-size: 12px;
  margin-left: 10px;
}
.strength-percent {
  flex: 1;
  height: 5px;
  border-radius: 10px;
  display: block;
  background: transparent;
}

.strength-percent span {
  display: block;
  height: 100%;
  transition: all 0.4s;
  border-radius: 10px;
}

.none {
  background: transparent;
  width: 0%;
}

.weak {
  background: rgba(255, 0, 0, 0.5);
  width: 33%;
}

.average {
  background: hsla(49, 100%, 50%, 0.5);
  width: 66%;
}

.strong {
  background: rgba(0, 128, 0, 0.5);
  width: 100%;
}
.pass-input-active{
  color: #000;
  background: #fff;
}
.pass-input-passive{
  border-radius: 15px;
}

.toggle-icon-active{
  right: 8px;
}
.toggle-icon-passive{
  font-size:25px;
}

.cursor_default {
  cursor: default;
}

.confirmRules {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.form__rules {
  width: 30px;
  height: 30px;
}

button[type="submit"]:active:not(:disabled),
input[type="button"]:active,
input[type="submit"]:active {
  transition: 0.3s all;
  transform: translateY(3px);
  border: 1px solid transparent;
  opacity: 0.8;
}

input:disabled {
  opacity: 0.4;
}

input[type="button"]:hover {
  transition: 0.3s all;
}

button[type="submit"],
input[type="button"],
input[type="submit"] {
  -webkit-appearance: none;
}

.App {
  max-width: 600px;
  margin: 0 auto;
}

button[type="button"] {
  display: block;
  appearance: none;
  background: #333;
  color: white;
  border: none;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 4px;
}

button {
  display: block;
  appearance: none;
  margin-top: 40px;
  border: 1px solid #333;
  margin-bottom: 20px;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 4px;
}